@import '~cssVars';

.signUp {
  position: relative;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10vw;
  margin-bottom: 30px;
  border-radius: 4px;
  padding: 25px;
  box-shadow: 0 0 12px rgba(#000, .4);

  h2,
  h3 {
    text-align: center;
    font-weight: 700;
    font-family: $f-title; }

  h2 {
    margin-bottom: 30px;
    font-size: rem(24);
    letter-spacing: 1px; }

  h3 {
    margin-bottom: 20px; }

  button {
    display: block;
    width: 100%;
    max-width: 150px;
    border-radius: 4px;
    margin-top: 35px;
    margin-left: auto;
    margin-right: auto;
    height: 40px;
    line-height: 38px;
    color: #FFF; } }


.fieldsText {
  margin-bottom: 10px;
  input {
    outline: none;
    width: 100%;
    padding: 10px 15px;
    border-radius: 4px;
    margin-bottom: 25px;
    transition: box-shadow .2s;

    &:focus {
      box-shadow: 0 0 0 1px rgba($c-accent, .7); } } }

.errorField {
  box-shadow: 0 0 0 1px rgba(#f00, .5); }

.fieldsRadio {
  display: flex;
  justify-content: space-between;
  align-items: center;
  // max-width: 200px
  margin-left: auto;
  margin-right: auto;

  input {
    position: absolute;
    opacity: 0;
    z-index: -10;
    outline: none;
    width: 0;
    height: 0; }

  label {
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: .7;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    padding: 6px;
    cursor: pointer;
    transition: opacity .2s, box-shadow .3s;

    &:hover {
      opacity: 1; }

    img {
      max-height: 100%; } }

  input:checked + label {
    opacity: 1;
    box-shadow: inset 0 0 0 2px $c-accent; } }

.error {
  margin-bottom: 15px;
  font-size: rem(14);
  text-align: center;
  color: #f00; }

.loginLink {
  display: block;
  margin-top: 30px;
  text-align: center;
  font-weight: 500;
  color: $c-text;
  transition: color .2s;

  &:hover {
    color: $c-accent; } }

.signUp_loading:after :global {
  content: '';
  display: block;
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(#FFF, .5);
  background-image: url('~decor/loader.gif');
  background-position: center;
  background-repeat: no-repeat; }



