// @import "vars"

*::-webkit-input-placeholder {
	color: $c-placeholder;
	opacity: 1; }

*:-moz-placeholder {
	color: $c-placeholder;
	opacity: 1; }

*::-moz-placeholder {
	color: $c-placeholder;
	opacity: 1; }

*:-ms-input-placeholder {
	color: $c-placeholder;
	opacity: 1; }

* {
	box-sizing: border-box; }

body input:focus:required:invalid,
body textarea:focus:required:invalid {
	color: $c-placeholder_invalid; }

body input:required:valid,
body textarea:required:valid {
	color: $c-placeholder_active; }

input:focus,
textarea:focus {
	color: $c-placeholder_active; }

html {
	overflow-x: hidden; }

body {
	min-height: 100%;
	background: $c-bg-body;
	min-width: $w-min;
	position: relative;
	overflow-x: hidden;
	font-family: $f-main;
	font-size: $fs-main;
	color: $c-text; }

img {
	max-width: 100%;
	// max-height: 100%
	height: auto;
	vertical-align: bottom {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none; } }

a, a:hover, a:focus {
	text-decoration: none; }

a {
	color: $c-link;
	transition: color .2s; }

li {
	outline: none; }

input[type=submit],
button {
	cursor: pointer; }

p {
	// @include media-max(1199px)
	// 	font-size: $fs-main_tablet

	// @include media-max(599px)
 }	// 	font-size: $fs-main_phone


button {
	font-family: $f-main; }

textarea {
	resize: none; }



